import { styled } from '@mui/system';
import useMediaQuery from '@mui/material/useMediaQuery';
import Typography from '@mui/material/Typography';
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';
import { useParallax } from 'react-scroll-parallax';
import { Typing } from 'typing-effect-reactjs';

import logo from './logo2.svg';
import './Banner.css';

export default function Banner() {
  const isDesktop = useMediaQuery('(min-width:600px)');
  const logoParallax = useParallax({
    easing: 'easeOutQuad',
    translateX: [-16, 0],
    translateY: [-64, 0],
    rotate: [-15, 15],
  });
  const StyledComponent = styled('div')({
    textAlign: 'center',
    height: (window.innerHeight + 128) + 'px',
    width: '100%'
  });

  
  const info = ['오랜만이에요.', '많이 기다리셨죠?', '곧 만나요 !' , '2023 Spring Camp'];
  return (
    <div>
      <StyledComponent>
        <Typography variant={isDesktop ? 'h1' : 'h4'} component={isDesktop ? 'h2' : 'h5'} 
          style={{position: 'relative', top: (window.innerHeight/2-100) + 'px',}}>
          <Typing 
            text={info}
            typeSpeed={200}
            styleClass='margin-none'
            cursorColor='white'
          />
        </Typography>
        <div className="arrow bounce">
        <KeyboardDoubleArrowDownIcon sx={{ fontSize: 60 }}
          style={{
            position: 'relative',
            top: (window.innerHeight/3 * 2) + 'px',
            color: 'white',
          }} />
        </div>
      </StyledComponent>
      <div ref={logoParallax.ref} style={{ marginTop: '30%'}}>
        <img src={logo} className="App-logo" alt="logo" style={{height: '40vmin'}}/>
      </div>
    </div>
  );
}