import { styled } from '@mui/system';
import useMediaQuery from '@mui/material/useMediaQuery';
import Typography from '@mui/material/Typography';

import { useParallax } from 'react-scroll-parallax';
import Typewriter from 'typewriter-effect/dist/core';
import logo from './logo2.svg';
import map from './map.png';

export default function Where() {
  const isDesktop = useMediaQuery('(min-width:600px)');
  const StyledComponent = styled('div')({
    textAlign: 'center',
    marginBottom: isDesktop ? '10%' : '20%',
    marginTop: isDesktop ? '10%' : '20%'
  });
  const scale = isDesktop ? 0.5 : 1;
  return (
    <section id="where">
      <StyledComponent>
        <div ref={useParallax({
          easing: 'easeIn',
          translateY: [-32, 0],
          opacity: [0, 1],
          onEnter: () => {
            new Typewriter('#location', {
              strings: ['을지로입구역 SKT Tower 4층 수펙스홀'],
              autoStart: true,
              pauseFor: 60000,
            });
          },
        }).ref}>
          <Typography 
            variant={isDesktop ? 'h1' : 'h3'} component={isDesktop ? 'h3' : 'h5'}
            sx={{marginTop: 4, marginBottom: 4}}
          >
            Location
          </Typography>
          <div id="location" style={{fontSize: '20px'}}></div>
          <div>
            <img id="bgLogo" src={logo} alt="" style={{
              position: 'absolute',
              transform: `rotate(15deg) scale(${scale})`,
              opacity: 0.1,
            }} />
          </div>
          <a href="https://naver.me/5rsMBlxb" target="_blank" rel="noreferrer">
            <img src={map} alt="map" style={{
              height: '100vmin',
              width: '100%',
              marginTop: 32, 
              marginBottom: 4,
              objectFit: 'none',
              objectPosition: '55% 50%',
            }}/>
          </a>
        </div>
      </StyledComponent>
    </section>
  );
}