import { styled } from '@mui/system';
import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
import useMediaQuery from '@mui/material/useMediaQuery';
import { NEW_COLORS } from '../Color';

export default function Contact() {
  const isDesktop = useMediaQuery('(min-width:600px)');
  const StyledComponent = styled('div')({
    textAlign: 'center',
    verticalAlign: 'bottom',
    height: 128,
    background: NEW_COLORS.background,
  });

  const pointColor = NEW_COLORS.pointBright;
  
  return (
    <StyledComponent>
      <Box sx={{ display: 'inline-flex' }}>
        <Link href="https://springcamp.ksug.org" underline="none" color={pointColor}>@springcamp</Link>
        <Link href="mailto:springcamp@ksug.org" underline="none" color={pointColor} sx={{marginLeft: isDesktop ? 48 : 4}}>springcamp@ksug.org</Link>
      </Box>
    </StyledComponent>
  );
}